.app-wrapper {
  display: grid;
  max-width: 1200px;
  min-height: 100vh;
  height: 100%;
  margin: 0 auto;

  grid-template-areas:
    "h h"
    "n c";
  grid-template-rows: 100px 1fr;
  grid-template-columns: 2fr 10fr;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.5);

  /*grid-gap: 10px;*/
}

.app-wrapper-content {
  grid-area: c;
  background-color: whitesmoke;
  color: black;
}
